import React, { useState, useContext } from 'react';
import { useNavigate, Link } from "react-router-dom";
import { UserContex } from "../App";
import { GoogleLogin } from 'react-google-login';

function Login() {
    const { state, dispatch } = useContext(UserContex);

    let navigate = useNavigate();
    const [emailU, setEmailU] = useState({
        email: "", password: ""
    });
    let name, value;

    const handleInputLogin = (e) => {
        // console.log(e);
        name = e.target.name;
        value = e.target.value;

        setEmailU({ ...emailU, [name]: value });
    }
    //

    async function PostDataLogin(e) {
        e.preventDefault();
        // console.log("click function activated");
        const { email, password } = emailU;

        const res = await fetch("/login", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                email: email, password: password
            })
        });

        if(res.status === 429){
            window.alert("Too many requests from same IP,Please wait few seconds and try again")
        }
        const data = await res.json();
        console.log(data); // showing error/responce
        
        if (res.status === 200) {
            console.log("Login successful");
            // to save authentication data in local and session storage 
            console.log("res:",res,"if an cokkie? ",data);
                let date = new Date()
                let tokenExpiry =new Date();
                tokenExpiry.setMinutes(date.getMinutes() + 10);
                localStorage.setItem('tokenExpiry',`${tokenExpiry.valueOf()}`);
               console.log(date,tokenExpiry);
            
            dispatch({ type: 'USER', payload: true });
            navigate("...", { replace: true });
            navigate('/');
        } else {
            
            console.log(data);
        }

    }
    const onSuccessfulGoogleLogin = async (req) => {
        console.log("GoogleSuccess1");
        const res = await fetch("/googleAuth", {// send google token=>server to verify
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "tokenId": req.tokenId
            })
        });
        const data = await res.json();
        console.log(data);
        dispatch({ type: 'USER', payload: true });
        navigate("...", { replace: true });
        navigate('/');

    };
    const onFailGoogleLogin = async (err) => {
        console.log("GoogleFail1",err);
    }

    return (
        <div className="back">
            <div id="login-box">
                <form className="login-form" method="POST" id="login-form">
                    <div className="right">
                        <h1 className="loginH1">Login here</h1>
                        <input type="text" name="email" id="email" placeholder="E-mail" value={emailU.email} onChange={handleInputLogin} />
                        <input type="password" name="password" id="password" placeholder="Password" autoComplete="off" value={emailU.password} onChange={handleInputLogin} />
                        <input type="submit" name="signup_submit" value="Login" onClick={PostDataLogin} />
                    </div>
                </form>
                <div className="left-login">
                    <span className="loginwith "></span>

                    <button className="social-signin facebook" >Log in with facebook</button>
                    <button className="social-signin twitter">Log in with Twitter</button>
                    <div >
                        <GoogleLogin
                            clientId="981651724564-lhl0q94397rk4s46i2311ot6h6k6j5bm.apps.googleusercontent.com"
                            render={renderProps => (
                                <button onClick={renderProps.onClick} className="social-signin google" disabled={renderProps.disabled}>Sign In with Google</button>
                            )}
                            buttonText="Login"
                            onSuccess={onSuccessfulGoogleLogin}
                            onFailure={onFailGoogleLogin}
                            cookiePolicy={'single_host_origin'}
                        />
                    </div>
                </div>
                <div className="or">OR</div>
            
                <div className="bottom"><Link to="/forgot-password"><span>Forgot password?</span></Link></div>
            </div>
        </div>
    )
};

export default Login;
