import React, { useState } from 'react';
import {useNavigate } from "react-router-dom";


const SignUp = () => {
    let navigate = useNavigate()

    const [user, setUser] = useState({
        username: "", email: "", password: "", cpassword: ""
    });
    let name, value;

    const handleInput = (e) => {
        // console.log(e);
        name = e.target.name;
        value = e.target.value;

        setUser({ ...user, [name]: value });
    }

    async function PostData(e) {
        e.preventDefault();
        // console.log("click function activated");
        const { username, email, password, cpassword } = user;

        const res = await fetch("/register", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                username: username, email: email, password: password, cpassword: cpassword
            })
        });

        // console.log(res.body);
        // console.log("fetch sent");
        const data = await res.json();
        // console.log(data); // showing error/responce
        // console.log(res.status);
        if (res.status === 422 || !data) {
            console.log(data);
        } else {
            console.log("activation of mail required");
            // this.context.history.push('/path');
            // history.go("/login");
            navigate("...", { replace: true });
            navigate('/activate-account/');
        }

    }

    return (
        <>
            <div className="back">
                <div id="login-box">
                    <form className="register-form" method="POST" id="register-form">
                        <div className="left">
                            <h1 className="signupH1">Sign up</h1>
                            <input type="text" name="username" id="username" placeholder="Username" autoComplete="off" value={user.username} onChange={handleInput} />
                            <input type="text" name="email" id="email" placeholder="E-mail" autoComplete="off" value={user.email} onChange={handleInput} />
                            <input type="password" name="password" id="password" placeholder="Password" autoComplete="off" value={user.password} onChange={handleInput} />
                            <input type="password" name="cpassword" id="cpassword" placeholder="Retype password" autoComplete="off" value={user.cpassword} onChange={handleInput} />

                            <input type="submit" name="signup" value="Sign me up" id="signup" onClick={PostData} />
                        </div>
                    </form>
                    <div className="right">
                        <span className="loginwith my-3">Sign in with<br />social network</span>

                        <button className="social-signin facebook">Log in with facebook</button>
                        <button className="social-signin twitter">Log in with Twitter</button>
                        <button className="social-signin google">Log in with Google+</button>
                    </div>
                    <div className="or">OR</div>
                </div>
            </div>
        </>
    )
}

export default SignUp