import React, { useState } from 'react';

const ForgotPassword = () => {
    const [forgotPassRender, setForgotPassRender] = useState(true);
    const [emailR, setEmailR] = useState({
        email: ""
    }); // add intial state if more rows are added(i.e xyz)
    let name, value;
    const handleInputLogin = (e) => {
        // to handle form vlaues in react hook 
        //in the render, set input.value = email.xyz
        name = e.target.name;
        value = e.target.value;
        setEmailR({ ...emailR, [name]: value });
    }
    const { email } = emailR;
    const forgotPass = async (e) => {// send request to server=user have forgot password, generate authToken
        e.preventDefault();
        const res = await fetch("/forgot-password", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ email: email })
        });
        const data = await res.json();
        console.log(data);
        if (res.status === 429) {
            window.alert("To many requests")
        }
        if (res.status === 200) {
            setForgotPassRender(false);
            console.log("forgotPassRender=", forgotPassRender)
        } else if (res.status === 404) {
            window.alert("Invalid user")
        }
    }

    // console.log(forgotPassRender, "<=forgotPassRender")
  
    return (
        <div className="back">
            <div id="login-box">
                <form className="login-form" method="POST" id="login-form">
                    <div className="center">
                        {
                            // Intial render
                            forgotPassRender && <>
                                <h1 className="loginH1">Enter your registered Email</h1>
                                <input type="text" name="email" id="email" placeholder="E-mail" value={email} onChange={handleInputLogin} />
                                <input type="submit" name="signup_submit" value="Verify" onClick={forgotPass} />
                                </>
                        }
                        {
                            //only render if status 200
                            forgotPassRender || <>
                             <div>Please check your registered email for reset password Link</div>
                            </>
                        }

                    </div>
                </form>
            </div>
        </div>
    )
    

}

export default ForgotPassword;
