import React, {useEffect} from 'react'
import { useParams , useNavigate} from "react-router-dom";

const AuthMail = () => {// send slug to back end and get register user
    const { slug } = useParams();
    let userMessage;
    console.log("slug:", slug);
    let navigate = useNavigate();
    
    const postToken = async ()=>{
        try{
            userMessage="Please wait while we activate your acoount"
        const res = await fetch("/AuthMail", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({signUpToken:slug})
        });
        const data = res.json();
        console.log(data);
        if(res.status === 201){
            navigate("...", { replace: true });
            navigate('/signup-success');
        }
    }catch(err){
        console.log(err);
    }   
    }
    
    useEffect(() => {
        if(slug){
        postToken();
    }
    }, []);
    
    return (
        <div>
            {userMessage}
        </div>

    )
}

export default AuthMail
