import React, { useEffect, useState, useContext } from 'react';
import { useNavigate } from "react-router-dom";
import user_profile from "../images/user-profile-default.png"
import { UserContex } from "../App";

const initialState = () => {
    console.log("intial state called");
    return { email: "" };
}
const About = (props) => {
    const { state, dispatch } = useContext(UserContex);

    let navigate = useNavigate();
    const [userData, setUserData] = useState(() => initialState());
    // console.log(userData);

    const callAboutPage = async () => {
        try {
            console.log("call about page render")
            const res = await fetch('/about', {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-type": "application/json"
                },
                credentials: "include"
            });
            if(res.status === 429){
                window.alert("Too many requests from same IP,Please wait few seconds and try again")
            }
            const data = await res.json();
            console.log(data);
            await setUserData(data);
            // dispatch({ type: 'USER', payload: true });

            // console.log(userData);//idk undefined
            if (!res.status === 200) {
                console.log("unauthorised");
                const error = new Error(res.error);
                throw error;
            }else{
                
            }
        } catch (error) {
            console.log("error45:", error);
            console.log("error 401 ");
            dispatch({ type: 'USER', payload: false });
            window.alert("Please Login");
            navigate("...", { replace: true });
            navigate('/login');
        }
    }
    useEffect(() => {
        callAboutPage();
    }, []);
    let accountPic;
    if(!userData.imgUrl){
        accountPic= user_profile;
    }else{
        accountPic = userData.imgUrl
    }

    return (
        <>
            <div className="back">
                <div id="login-box">
                    <form className="login-form" method="GET" id="login-form">
                        <div className="right">
                            <h1 className="loginH1">Your info</h1>
                            <p>Email:{userData.email}</p>
                            <p>Username:{userData.username}</p>
                            {/* <img src={userData.imgUrl} alt="No Photo " /> */}
                        </div>
                    </form>
                    <div className="left-login">
                        <span className="loginwith "></span>
                        {/* <h1> Id Photo</h1> */}
                        <img src={accountPic} width="100px"alt="" />
                    </div>
                    <div className="or">/</div>
                </div>
            </div>
        </>
    )
}

export default About
