import React, { useState } from 'react';
import { useParams, Link } from "react-router-dom";


const ResetPassword = () => {

    // var resetPassRender = true;
    const [resPassRender, setResPassRender] = useState(true);
    // var passverified = false;
    const [passverified, setPassverified] = useState(false);
    const { slug } = useParams();
    const slugArr = slug.split("-NovelPlusUser=");
    console.log("token:", slugArr[0], "email:", slugArr[1])
    //
    const [newPassword, setNewPassword] = useState({
        password: "", cpassword: ""
    }); // add intial state if more rows are added(i.e xyz)
    let name, value;
    const handleInput = (e) => {
        // to handle form vlaues in react hook 
        //in the render, set input.value = password.xyz
        name = e.target.name;
        value = e.target.value;
        setNewPassword({ ...newPassword, [name]: value });
    }
    const { password, cpassword } = newPassword;
    const resetPass = async (e) => {// send request to server=user requested for pwd reset
        e.preventDefault();
        if (password === cpassword) {
            const res = await fetch("/reset-password", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({ email: `${slugArr[1]}`, password: password, resetToken: `${slugArr[0]}` })
            });
            const data = await res.json();
            console.log(data);


            if (res.status === 200) {
                setResPassRender(false); // resetPassRender = false;
                setPassverified(true);// passverified = true;
            } else if (res.status === 400) {
                window.alert("Invalid user");
                setResPassRender(false);// resetPassRender = false;
                setPassverified(true);// passverified = false;
            }
        } else { console.log("passwordNot Match") }
    }


    if (passverified) {
        return <>
            <div className="back">
                <div id="login-box">
                    <div className="login-form" >
                        <div className="center">Password reset successful <br/>
                        <span><Link to="/login"><span>login here</span></Link>
                        </span>
                        </div>
                    </div>
                </div>
            </div>

        </>
    } else if (resPassRender && slugArr[0] && slugArr[1]) {
        return (
            <div className="back">
                <div id="login-box">
                    <form className="login-form" method="POST" id="login-form">
                        <div className="left">
                            <h1 className="loginH1">Please enter new password</h1>
                            <input type="password" name="password" id="password" placeholder="Password" autoComplete="off" value={newPassword.password} onChange={handleInput} />
                            <input type="password" name="cpassword" id="cpassword" placeholder="Retype password" autoComplete="off" value={newPassword.cpassword} onChange={handleInput} />
                            {password.length < 8 ? <div className="small">password too short</div> : password !== cpassword ? <div className="small">Password do not match</div> : <input type="submit" name="signup_submit" value="Reset" onClick={resetPass} />}

                        </div>
                    </form>
                </div>
            </div>
        )
    } else {
        return <div className="back">
            <div id="login-box">
                <form className="login-form" method="POST" id="login-form">
                    <div className="center">
                        <div>invalid or expired link</div>
                    </div>
                </form>
            </div>
        </div>

    }
}

export default ResetPassword
